@charset "UTF-8";
@import "../../../style/theme/default/var.css";

/*@component-namespace template{
  @component table {
  }
}*/
.demo-icon .source > i {
  font-size: 24px;
  color: var(--theme-font-color-primary);
  margin: 0 20px;
  font-size: 1.5em;
  vertical-align: middle;
}

.demo-icon .source > button {
  margin: 0 20px;
}

.icon-list {
  overflow: hidden;
  list-style: none;
  padding: 0;
  border: solid 1px var(--theme-border-color-primary);
  border-radius: 4px;
}
.icon-list li {
  float: left;
  width: 16.66%;
  text-align: center;
  height: 120px;
  line-height: 120px;
  font-size: 13px;
  transition: color .15s linear;

  border-right: 1px solid  var(--theme-border-color-primary);
  border-bottom: 1px solid  var(--theme-border-color-primary);
  margin-right: -1px;
  margin-bottom: -1px;

@utils-vertical-center;

& span {
    display: inline-block;
    line-height: normal;
    vertical-align: middle;
    /*font-family: 'Helvetica Neue',Helvetica,'PingFang SC','Hiragino Sans GB','Microsoft YaHei',SimSun,sans-serif;*/
    color: var(--theme-font-color-primary);
  }
& i {
    display: block;
    font-size: 24px;
    margin-bottom: 15px;
    color: var(--theme-color-primary);
  }
 & b{
  display: block;
  font-weight: normal;
   }
}



